@import "./_variables";

.NoContentsMessage
{
	color: lighten($bgColor0, 10%);
	font-size: 50px;
	font-weight: bolder;

	filter: drop-shadow(0 0 10px black);

	position: absolute;
	top: 0; left: 0; bottom: 0; right: 0;
	line-height: 100vh;

	text-align: center;

	pointer-events: none;
}

.TabsList
{
	display: flex;
	justify-content: center;

	margin-top: 20px;

	.TabButton
	{
		position: relative;

		width: 200px;
		height: 75px;
		line-height: 75px;
		margin: 0 10px;
		
		display: flex;
		justify-content: center;
		align-content: center;

		cursor: pointer;

		.TabButtonTitle
		{
			color: darken(white, 50%);
			transition: color $t;
			font-size: 1.5rem;
		}

		.TabButtonBorder
		{
			position: absolute;
			bottom: 0;

			width: 25%;

			border-bottom: 2px solid darken(white, 50%);
			transition: border-bottom-color $t, width $t;
		}

		&:hover
		{
			.TabButtonTitle
			{
				color: white;
			}

			.TabButtonBorder
			{
				width: 100%;
				border-bottom-color: white;
			}
		}

		&.Active
		{
			.TabButtonTitle
			{
				color: $color0;
			}

			.TabButtonBorder
			{
				width: 100%;
				border-bottom-color: $color0;
			}
		}
	}
}