@import "_variables.scss";

.Header
{
	background-color: $bgColor1;
	border: 20px solid $bgColor1;
	border-top-width: 15px;

	.MainHeader
	{
		margin-left: 10px;
		color: white;
		font-size: 50px;
		font-weight: bolder;
	}

	.SearchBar
	{
		position: relative;

		input
		{
			background: #242424;
			color: white;
			border: none;
			border-radius: 5px;
	
			font-size: 1.5rem;
			
			position: relative;
			top: -8px;
	
			width: 25px;
			margin-left: 40px;
			margin-bottom: -1000px;
			height: 35px;
			padding: 5px 10px;
	
			transition: width $t, background-color $t;
	
			&:focus
			{
				background-color: #4e4e4e;
			}
		}

		img
		{
			display: inline-block;
			width: 25px;
			position: relative;

			top: -5px;
			left: -35px;

			pointer-events: none;
			filter: invert(35%);

			transition: filter $t;
		}

		&.Focused
		{
			input
			{
				width: 25%;
			}

			img
			{
				pointer-events: all;
				cursor: pointer;
				filter: invert(100%);
			}
		}
	}
	
	.HeaderItems
	{
		float: right;

		.HeaderItemContainer
		{
			display: inline-block;

			position: relative;
			width: 75px;
			height: 75px;

			cursor: pointer;
				
			&:hover
			{
				.HeaderItem
				{
					width: 250px;
					background-color: white;
			
					.HeaderItemContent
					{
						color: black;
						width: 250px;
					}
					
					.HeaderItemIcon
					{
						transform: scale(1.2);
						filter: none;
					}
				}
			}

			.HeaderItem
			{
				position: absolute;
				overflow: hidden;
				pointer-events: none;
				right: 0;
				
				width: 50px;
				height: 50px;
				
				border: 10px solid transparent;
				border-radius: 5px;

				background-color: transparent;
				border-color: transparent;
			
				transition: width $t, background-color $t;
			
				.HeaderItemContent
				{
					color: transparent;
					line-height: 50px;

					margin-right: 50px;
					display: inline;
					border: 20px solid transparent;
			
					transition: color $t, width $t;
				}
				
				.HeaderItemIcon
				{
					position: absolute;
					top: 10px;
					right: 10px;
					width: 30px;
					filter: invert(100%);
			
					transition: transform $t, filter $t;
				}
			}
		}
	}
}