@import "../components/_variables";

.Main
{
	.Fields
	{
		margin: 50px;

		.ContactField
		{
			color: white;
	
			width: 100%;
			margin-bottom: 50px;
	
			display: flex;
			flex-direction: column;
	
			.Title
			{
				font-size: 2rem;
				font-weight: bold;

				&.Note
				{
					font-size: 1.5rem;
					font-weight: lighter;

					color: darken(white, 50%);

					border-left: 2px solid $pluginColor;
					margin-left: 15px;
					padding-left: 15px;
				}
			}
	
			input, textarea
			{
				font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
				
				margin-top: 20px;
				border: none;
				border-radius: 5px;
				padding: 15px;
				font-size: 1.2rem;
			}
		}
	}

	.SendButton
	{
		color: white;
		background: $pluginColor;

		border-radius: 5px;
		margin: 50px;
		padding: 15px;

		text-align: center;
		font-size: 1.2rem;
	}
}