@import "./_variables";

.ModalContainer
{
	background-color: rgba(black, 0.8);

	display: flex;
	align-content: center;
	justify-content: center;

	position: fixed;
	top: 0; left: 0; bottom: 0; right: 0;
	opacity: 0;

	transition: opacity $t;

	pointer-events: none;

	.Modal
	{
		transform: scale(0);
		transition: transform $t;

		> :first-child
		{
			position: relative;
			top: 50%;
			transform: translateY(-50%);
		}

		.ClipboardModal
		{
			background: $bgColor1;
			color: white;
			font-size: 1.5rem;
	
			padding: 35px;
			border-radius: 5px;
		}

		.ImageModal
		{
			background-position: center;
			background-size: contain;
			background-repeat: no-repeat;

			width: 90vw;
			height: 90vh;
		}
	}

	&.Active
	{
		opacity: 1;

		pointer-events: all;

		.Modal
		{
			transform: scale(1);
		}
	}
}