@import "../components/_variables";

.Items
{
	display: flex;
	flex-wrap: wrap;
	margin: 2.5%;

	.Item
	{
		--primaryColor: white;

		width: 45%;
		height: 200px;
		margin: 2.5%;
		border-radius: 5px;
		border-bottom: 3px solid var(--primaryColor);

		position: relative;

		color: white;
		background: #242424;

		.ItemHeader
		{
			display: flex;

			.ItemLabel
			{
				margin-left: 10px;
				font-size: 2rem;
				font-weight: bold;
			}

			.ItemFlags
			{
				margin-left: auto;

				.ItemFlag
				{
					width: 30px;
					margin: 10px;

					filter: invert(100%);
					cursor: pointer;
				}
			}
		}

		.ItemDescription
		{
			opacity: 0.8;
			margin: 10px;
			overflow: hidden;
			max-height: 55%;
		}

		.ItemFooter
		{
			background: var(--primaryColor);

			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			padding-bottom: 7px;
			
			display: flex;
			
			height: 50px;
			
			opacity: 0;
			transition: opacity $t;

			.ItemButton
			{
				position: relative;
				cursor: pointer;

				width: 125px;
				border-radius: 5px;

				margin: 10px 10px 0 10px;
				background: rgba(black, 0.15);

				span
				{
					position: absolute;
					top: 0; left: 0; bottom: 0; right: 0;

					text-align: center;
					line-height: 40px;
				}

				&.Disabled
				{
					opacity: 0.5;
					cursor: not-allowed;
				}
			}
		}

		&:hover
		{
			.ItemFooter
			{
				opacity: 1;
			}
		}

		&.Plugin, &.Bot
		{
			--primaryColor: #{$pluginColor};

			.Tooltip
			{
				--backgroundColor:#{$pluginColor};
				--textColor: white;
			}
		}

		&.Outdated
		{
			$c: #6e6e6e;

			--primaryColor: #{$c};
			background-color: darken($c, 25%);

			.Tooltip
			{
				--backgroundColor: #{$c};

				.TooltipText
				{
					color: white;
				}
			}
		}

		&.Deprecated
		{
			$c: #8a2b2b;

			--primaryColor: #{$c};
			background-color: darken($c, 25%);

			.Tooltip
			{
				--backgroundColor: #{$c};

				.TooltipText
				{
					color: white;
				}
			}
		}
	}
}

.Modal
{
	.MoreInfoModal
	{
		width: 600px;
		height: 400px;

		background: $bgColor1;
		color: white;
		font-weight: 650;

		border-radius: 5px;
		border: 2px solid $pluginColor;
		filter: drop-shadow(0 0 10px $pluginColor);

		display: flex;
		flex-direction: column;

		.InfoName
		{
			background: $pluginColor;
			padding: 5px;
			text-align: center;
			font-weight: bold;
			letter-spacing: 1px;
			font-size: 1.1rem;
		}

		.InfoDesc
		{
			margin: 15px;
		}

		.InfoMedia
		{
			position: relative;
			overflow-x: scroll;
			overflow-y: hidden;

			flex: content;
			margin-bottom: 50px;
			white-space: nowrap;

			.InfoPreview
			{
				display: inline-block;
				width: 250px;
				height: 65%;
				border-radius: 5px;
				margin: 5px;
				cursor: pointer;
			}
		}

		.InfoFooter
		{
			position: absolute;

			bottom: 0;
			margin-bottom: 15px;

			.InfoFooterItem
			{
				margin: 20px;
			}
		}

		.Sub
		{
			font-weight: normal;
			opacity: 0.5;
		}
	}
}