@import "components/_variables";

html, .App
{
	background-color: $bgColor0;
	background-size: cover;
}

a { color: $color0 }

.App
{
	min-height: 100vh;
				
	-moz-user-select: none;
	-webkit-user-select: none;
	overflow-x: hidden;

	.Main
	{
		min-height: 100vh;

		
	}

	.Footer
	{
		background: $bgColor1;
		position: relative;

		left: 0;
		bottom: 0;
		right: 0;

		height: 50px;
		
		display: flex;
		align-items: center;
		justify-content: center;

		.FooterItems
		{
			color: white;

			.FooterItems
			{

			}
		}
	}

	.ErrorText
	{
		color: red;
		font-size: 50px;
		font-weight: bolder;

		filter: drop-shadow(0 0 10px red);

		position: fixed;
		top: 0; left: 0; bottom: 0; right: 0;

		text-align: center;

		pointer-events: none;
	}
}