@import "../components/_variables";

.Main
{
	.ProjectList
	{
		position: relative;
		margin: 50px;
		margin-bottom: 0;
		display: flex;
		flex-direction: column;

		.Project
		{
			margin-bottom: 50px;
			color: white;
			background: $bgColor1;

			border-radius: 10px;

			.ProjectName
			{
				text-align: center;
				margin: 10px;
				font-size: 1.5rem;
				font-weight: bold;

				.Sub
				{
					color: $pluginColor;
				}
			}

			.ProjectDesc
			{
				margin: 20px;
			}

			.Slideshow
			{
				overflow: hidden;
				height: 800px;

				position: relative;

				pointer-events: none;

				.Images
				{
					position: absolute;
					top: 0; left: 0; bottom: 0; right: 0;

					margin: 20px;
					margin-top: 0;
					border-radius: 5px;

					overflow: hidden;

					.Image
					{
						position: absolute;
						top: 0; left: 0; bottom: 0; right: 0;

						background-position: center;
						background-size: cover;

						opacity: 0;
						transition: opacity $t;

						&.Active
						{
							opacity: 1;
							
							pointer-events: all;
							cursor: pointer;
						}
					}
				}

				.Buttons
				{
					filter: invert(100%);
					z-index: 10;

					height: 100%;

					.Button
					{
						pointer-events: all;
						cursor: pointer;

						position: absolute;
						width: 50px;

						top: 50%;
						transform: translateY(-50%);

						transition: width $t;

						&.Left { left: 50px; }
						&.Right { right: 50px; }

						&:hover { width: 70px; }
					}
				}
			}

			.ProjectButtons
			{
				display: flex;
				z-index: 10;

				.Button
				{
					background: lighten($bgColor1, 5%);
					padding: 20px;

					margin: 20px;
					margin-top: 0;
					margin-right: 0;
					border-radius: 5px;

					pointer-events: all;

					transition: background-color $t;

					&:not(.Disabled):hover
					{
						background-color: $pluginColor;
					}

					&.Disabled
					{
						cursor: not-allowed;
						opacity: 0.5;
					}
				}

				.Tooltip
				{
					margin-top: 75px;
				}
			}
		}
	}
}