@import "_variables.scss";

.Tooltip
{
	--backgroundColor: white;
	--textColor: black;

	position: absolute;
	background: var(--backgroundColor);
	border: 10px solid var(--backgroundColor);
	border-radius: 5px;
	
	word-wrap: break-word;
	width: max-content;

	pointer-events: none;
	z-index: 100;

	transition: opacity $t;

	filter: drop-shadow(0 0 10px black);

	&.Hidden
	{
		opacity: 0;
	}

	.TooltipArrow
	{
		border: 10px solid var(--backgroundColor);

		display: inline-block;
		position: relative;
		width: 0;
		height: 0;
		left: calc(50% + 10px);
		margin-left: -20px;
		top: -20px;

		transform: rotate(45deg);
	}

	.TooltipText
	{
		color: var(--textColor);
	}
}